<template>
  <div class="login"  @keydown.enter="handleSubmit">
    <div class="login-box">
      <Card :bordered="false">
        <p slot="title"><Icon type="ios-paper-plane"></Icon> 欢迎登录</p>
        <div class="form-box">
          <Form ref="loginForm" :model="form" :rules="rules">
            <FormItem prop="userName">
              <Input type="text" v-model="form.username" placeholder="请输入用户名" clearable>
                <span slot="prepend">
                  <Icon :size="16" type="ios-person"></Icon>
                </span>
              </Input>
            </FormItem>
            <FormItem prop="password">
              <Input :type="passwordType" v-model="form.password" placeholder="请输入密码" @on-focus="onPassword" clearable>
                <span slot="prepend">
                  <Icon :size="14" type="ios-lock"></Icon>
                </span>
              </Input>
            </FormItem>
            <FormItem>
              <Button @click="handleSubmit" type="primary" long>登录</Button>
            </FormItem>
          </Form>
        </div>
      </Card>
    </div>
    <vue-particles
      v-if="!isIe"
      color="#fff"
      :particleOpacity="0.6"
      :particlesNumber="80"
      shapeType="circle"
      :particleSize="5"
      linesColor="#dedede"
      :linesWidth="2"
      :lineLinked="true"
      :lineOpacity="0.6"
      :linesDistance="160"
      :moveSpeed="3"
      :hoverEffect="true"
      hoverMode="grab"
      :clickEffect="true"
      clickMode="push">
    </vue-particles>
  </div>
</template>

<script>
import {login} from 'api/login'
export default {
  name: 'login',
  data () {
    return {
      msg: 'login',
      passwordType: 'text',
      form: {
        username: '',
        password: ''
      },
      rules: {
        username: [
          { required: true, message: '账号不能为空', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '密码不能为空', trigger: 'blur' }
        ]
      },
      isIe: false
    }
  },
  created(){
    this._isIe()
  },
  methods:{
    _isIe(){
      if (!!window.ActiveXObject || "ActiveXObject" in window)
        this.isIe = true
      else
        this.isIe = false
    },
    onPassword(){ // 解决浏览器密码自动填充
      this.passwordType='password'
    },
    handleSubmit (){
      let params = {
        username: this.form.username,
        password: this.form.password
      }
      this._login(params)
    },
    _login(params) {
      login(params).then(res => {
        console.log(res)
        if(res.data.success){
          sessionStorage.removeItem('token')
          sessionStorage.removeItem('username')
          sessionStorage.setItem('username', this.form.username);
          sessionStorage.setItem('token', res.data.data.token);
          // 跳转到token失效前访问中的页面
          let redirect = decodeURIComponent(this.$route.query.redirect || 'index')
          this.$router.push({
            name: 'home_index'
          },()=>{
            this.$Message.success('登录成功！')
          })
        }else{
          // 登录失败清除token
          if(sessionStorage.token){
            sessionStorage.removeItem('token')
            sessionStorage.removeItem('username')
          }
          this.$Message.error(res.data.msg)
        }
      }).catch((err) => {
        console.log(err)
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
@bg: #1ac6ff;
.login{
  width: 100%;
  height: 100%;
  background-color: @bg;
  overflow: hidden;
  position: relative;
  .login-box{
    position: absolute;
    top: 50%;
    left: 50%;
    width: 360px;
    transform: translate(-50%, -50%); 
  }
  .form-box{
    padding: 10px 0 0;
  }
  #particles-js{}
}
</style>



